@import "../assets/sass/variables";

:host {
    display: block;
    height: 100%;

    .app-content {
      //padding: 20px;
      padding: 0 0 100px 0;
      height: 100%;
    }
    md-sidenav-layout {
      & > md-content {
        height: 100%;
      }
    }

    .app-sidenav {
      padding: 0;
      min-width: 100px;
    }

    .app-content md-checkbox {
      margin: 10px;
    }

    .app-toolbar-filler {
      flex: 1 1 auto;
    }

    .app-toolbar-menu {
      padding: 0 14px 0 14px;
      color: white;
    }

    .app-icon-button {
      box-shadow: none;
      user-select: none;
      background: none;
      border: none;
      cursor: pointer;
      filter: none;
      font-weight: normal;
      height: auto;
      line-height: inherit;
      margin: 0;
      min-width: 0;
      padding: 0;
      text-align: left;
      text-decoration: none;
    }

    .app-action {
      display: inline-block;
      position: fixed;
      bottom: 20px;
      right: 20px;
    }

    .app-spinner {
      height: 30px;
      width: 30px;
      display: inline-block;
    }

    .app-input-icon {
      font-size: 16px;
    }

    .app-list {
      border: 1px solid rgba(0,0,0,0.12);
      width: 350px;
      margin: 20px;
    }

    .app-progress {
      margin: 20px;
    }
}